<script>
  // @ts-nocheck

  import { onMount, onDestroy, createEventDispatcher, tick } from "svelte";
  import { fade } from "svelte/transition";
  import { LazyBrush } from "lazy-brush/src";
  import { brush_color_store } from "./Brushcolor";
  import ResizeObserver from "resize-observer-polyfill";

  const dispatch = createEventDispatcher();

  export let value;
  export let value_img;
  export let mode = "sketch";
  export let brush_color = "#0b0f19";
  export let brush_radius;
  export let source;

  export let width = 400;
  export let height = 200;
  export let container_height = 200;
  export let shape;

  let colorPickerEnabled = localStorage.setItem("colorPickerEnable", false);
  let transparentMask = localStorage.setItem("transparentMask", false);
  let posX = 0;
  let posY = 0;

  $: {
    if (shape && (width || height)) {
      width = shape[0];
      height = shape[1];
    }
  }

  let mounted;
  let isMaskMode;

  let catenary_color = "#aaa";

  let canvas_width = width;
  let canvas_height = height;

  $: mounted && !value && clear();

  let last_value_img;

  $: {
    if (mounted && value_img !== last_value_img) {
      last_value_img = value_img;

      clear();

      setTimeout(() => {
        if (source === "webcam") {
          ctx.temp.save();
          ctx.temp.translate(width, 0);
          ctx.temp.scale(-1, 1);
          ctx.temp.drawImage(value_img, 0, 0);
          ctx.temp.restore();
        } else {
          draw_cropped_image();
        }

        ctx.drawing.drawImage(canvas.temp, 0, 0, width, height);

        draw_lines({ lines: lines.slice() });
        trigger_on_change();
      }, 50);
    }
  }

  function mid_point(p1, p2) {
    return {
      x: p1.x + (p2.x - p1.x) / 2,
      y: p1.y + (p2.y - p1.y) / 2,
    };
  }

  const canvas_types = [
    {
      name: "interface",
      zIndex: 15,
    },
    {
      name: "drawing",
      zIndex: 11,
    },
    {
      name: "temp",
      zIndex: 12,
    },
    {
      name: "mask",
      zIndex: -1,
    },
    {
      name: "temp_fake",
      zIndex: -2,
    },
  ];

  let canvas = {};
  let ctx = {};
  let points = [];
  let lines = [];
  let mouse_has_moved = true;
  let values_changed = true;
  let is_drawing = false;
  let is_pressing = false;
  let lazy = null;
  let canvas_container = null;
  let canvas_observer = null;
  let line_count = 0;

  function draw_cropped_image() {
    if (!shape) {
      ctx.temp.drawImage(value_img, 0, 0, width, height);
      return;
    }

    let _width = value_img.naturalWidth;
    let _height = value_img.naturalHeight;

    const shape_ratio = shape[0] / shape[1];
    const image_ratio = _width / _height;

    let x = 0;
    let y = 0;

    if (shape_ratio < image_ratio) {
      _width = shape[1] * image_ratio;
      _height = shape[1];
      x = (shape[0] - _width) / 2;
    } else if (shape_ratio > image_ratio) {
      _width = shape[0];
      _height = shape[0] / image_ratio;
      y = (shape[1] - _height) / 2;
    } else {
      x = 0;
      y = 0;
      _width = shape[0];
      _height = shape[1];
    }

    ctx.temp.drawImage(value_img, x, y, _width, _height);
  }

  onMount(async () => {
    Object.keys(canvas).forEach((key) => {
      ctx[key] = canvas[key].getContext("2d");
    });

    await tick();

    if (value_img) {
      value_img.addEventListener("load", (_) => {
        if (source === "webcam") {
          ctx.temp.save();
          ctx.temp.translate(width, 0);
          ctx.temp.scale(-1, 1);
          ctx.temp.drawImage(value_img, 0, 0);
          ctx.temp.restore();
        } else {
          draw_cropped_image();
        }
        ctx.drawing.drawImage(canvas.temp, 0, 0, width, height);

        trigger_on_change();
      });

      setTimeout(() => {
        if (source === "webcam") {
          ctx.temp.save();
          ctx.temp.translate(width, 0);
          ctx.temp.scale(-1, 1);
          ctx.temp.drawImage(value_img, 0, 0);
          ctx.temp.restore();
        } else {
          draw_cropped_image();
        }

        ctx.drawing.drawImage(canvas.temp, 0, 0, width, height);

        draw_lines({ lines: lines.slice() });
        trigger_on_change();
      }, 100);
    }

    lazy = new LazyBrush({
      radius: brush_radius * 0.05,
      enabled: true,
      initialPoint: {
        x: width / 2,
        y: height / 2,
      },
    });

    canvas_observer = new ResizeObserver((entries, observer, ...rest) => {
      handle_canvas_resize(entries, observer);
    });
    canvas_observer.observe(canvas_container);

    loop();
    mounted = true;

    requestAnimationFrame(() => {
      init();
      requestAnimationFrame(() => {
        clear();
      });
    });
  });

  function init() {
    const initX = width / 2;
    const initY = height / 2;
    lazy.update({ x: initX, y: initY }, { both: true });
    lazy.update({ x: initX, y: initY }, { both: false });
    mouse_has_moved = true;
    values_changed = true;
  }

  onDestroy(() => {
    mounted = false;
    canvas_observer.unobserve(canvas_container);
  });

  export function redraw() {
    clear_canvas();
    if (value_img) {
      draw_cropped_image();
    }
    if (lines.length == 0) {
      dispatch("clear");
    }
    line_count = lines.length;
    draw_lines({ lines: lines }, true);
    trigger_on_change();
  }

  function redraw_image(_lines) {
    clear_canvas();

    if (value_img) {
      if (source === "webcam") {
        ctx.temp.save();
        ctx.temp.translate(width, 0);
        ctx.temp.scale(-1, 1);
        ctx.temp.drawImage(value_img, 0, 0);
        ctx.temp.restore();
      } else {
        draw_cropped_image();
      }

      if (!lines || !lines.length) {
        ctx.drawing.drawImage(canvas.temp, 0, 0, width, height);
      }
    }

    draw_lines({ lines: _lines });
    line_count = _lines.length;

    if (lines.length) {
      lines = _lines;
    }

    if (lines.length == 0) {
      dispatch("clear");
    }
  }

  export function clear_mask() {
    const _lines = [];

    redraw_image(_lines);
    trigger_on_change();
  }

  export function undo() {
    const _lines = lines.slice(0, -1);

    redraw_image(_lines);
    trigger_on_change();
  }

  let get_save_data = () => {
    return JSON.stringify({
      lines: lines,
      width: canvas_width,
      height: canvas_height,
    });
  };

  let draw_lines = ({ lines }) => {
    const brushMain = brush_color;
    lines.forEach((line) => {
      let { points: _points, brush_color, brush_radius } = line;

      if (mode === "mask") {
        draw_points({
          points: _points,
          brush_color: brushMain,
          brush_radius,
        });
      } else {
        draw_points({
          points: _points,
          brush_color,
          brush_radius,
        });
      }

      if (mode === "mask") {
        draw_fake_points({
          points: _points,
          brush_color: brushMain,
          brush_radius,
        });
      }

      points = _points;

      return;
    });
    saveLine({ brush_color, brush_radius });

    if (mode === "mask") {
      save_mask_line();
    }
  };

  let handle_draw_start = (e) => {
    e.preventDefault();
    const { x, y } = get_pointer_pos(e);

    if (!e.touches && e.button !== 0) return;

    colorPickerEnabled = localStorage.getItem("colorPickerEnable") === "true";

    if (colorPickerEnabled && mode !== "mask") {
      brush_color = getPixelColor(x, y);
      const brush_color_hex = rgbToHex(brush_color);
      brush_color_store.set(brush_color_hex);
      localStorage.setItem("colorPickerEnable", "false");
      return;
    }
    is_pressing = true;

    if (e.touches && e.touches.length > 0) {
      lazy.update({ x, y }, { both: true });
    }

    handle_pointer_move(x, y);
    line_count += 1;
  };

  let handle_draw_move = (e) => {
    e.preventDefault();

    if (localStorage.getItem("resetLines") === "true" && lines.length > 0) {
      localStorage.setItem("resetLines", "false");
      clear_mask();
    } else {
      localStorage.setItem("resetLines", "false");
    }

    if (localStorage.getItem("fillCanvasBrushColor") === "true") {
      fill_canvas_color();
      localStorage.setItem("fillCanvasBrushColor", false);
    }

    const { x, y } = get_pointer_pos(e);
    posX = x;
    posY = y;
    handle_pointer_move(x, y);
    localStorage.setItem("overCanvas", "true");
  };

  let handle_draw_end = (e) => {
    e.preventDefault();
    handle_draw_move(e);
    is_drawing = false;
    is_pressing = false;
    saveLine();

    transparentMask = localStorage.getItem("transparentMask") === "true";
    if (transparentMask) {
      redraw();
    }

    if (mode === "mask") {
      save_mask_line();
    }
  };

  let old_width = 0;
  let old_height = 0;
  let old_container_height = 0;
  let add_lr_border = false;

  let handle_canvas_resize = async () => {
    if (shape && canvas_container) {
      const x = canvas_container?.getBoundingClientRect();
      const shape_ratio = shape[0] / shape[1];
      const container_ratio = x.width / x.height;
      add_lr_border = shape_ratio < container_ratio;
    }

    if (width === old_width && height === old_height && old_container_height === container_height) {
      return;
    }
    const dimensions = { width: width, height: height };

    const container_dimensions = {
      height: container_height,
      width: container_height * (dimensions.width / dimensions.height),
    };

    await Promise.all([
      set_canvas_size(canvas.interface, dimensions, container_dimensions),
      set_canvas_size(canvas.drawing, dimensions, container_dimensions),
      set_canvas_size(canvas.temp, dimensions, container_dimensions),
      set_canvas_size(canvas.temp_fake, dimensions, container_dimensions),
      set_canvas_size(canvas.mask, dimensions, container_dimensions, false),
    ]);

    if (!brush_radius) {
      brush_radius = 20 * (dimensions.width / container_dimensions.width);
    }

    loop({ once: true });

    setTimeout(() => {
      old_height = height;
      old_width = width;
      old_container_height = container_height;
    }, 10);
    await tick();

    clear();
  };

  $: {
    if (lazy) {
      init();
      lazy.setRadius(brush_radius * 0.05);
    }
  }

  $: {
    if (width || height) {
      handle_canvas_resize();
    }
  }

  let set_canvas_size = async (canvas, dimensions, container, scale = true) => {
    if (!mounted) return;
    await tick();

    const dpr = window.devicePixelRatio || 1;
    canvas.width = dimensions.width * (scale ? dpr : 1);
    canvas.height = dimensions.height * (scale ? dpr : 1);

    const ctx = canvas.getContext("2d");
    scale && ctx.scale(dpr, dpr);

    canvas.style.width = `${container.width}px`;
    canvas.style.height = `${container.height}px`;
  };

  let get_pointer_pos = (e) => {
    const rect = canvas.interface.getBoundingClientRect();

    let clientX = e.clientX;
    let clientY = e.clientY;
    if (e.changedTouches && e.changedTouches.length > 0) {
      clientX = e.changedTouches[0].clientX;
      clientY = e.changedTouches[0].clientY;
    }

    return {
      x: ((clientX - rect.left) / rect.width) * width,
      y: ((clientY - rect.top) / rect.height) * height,
    };
  };

  function toggleColorPicker() {
    colorPickerEnabled = localStorage.getItem("colorPickerEnable") === "true";
    if (colorPickerEnabled && mode !== "mask") {
      for (const key in canvas) {
        canvas[key].style.cursor = "crosshair";
      }
    } else {
      for (const key in canvas) {
        canvas[key].style.cursor = "none";
      }
    }
  }

  let handle_pointer_move = (x, y) => {
    colorPickerEnabled = localStorage.getItem("colorPickerEnable") === "true";
    toggleColorPicker();
    if (colorPickerEnabled && mode !== "mask") {
      return;
    }

    lazy.update({ x: x, y: y });

    const is_disabled = !lazy.isEnabled();
    if ((is_pressing && !is_drawing) || (is_disabled && is_pressing)) {
      is_drawing = true;
      points.push(lazy.brush.toObject());
    }
    if (is_drawing) {
      points.push(lazy.brush.toObject());
      draw_points({
        points: points,
        brush_color,
        brush_radius,
      });

      if (mode === "mask") {
        draw_fake_points({
          points: points,
          brush_color,
          brush_radius,
        });
      }
    }
    mouse_has_moved = true;
  };

  let draw_points = ({ points, brush_color, brush_radius }) => {
    if (!points || points.length < 2) return;
    ctx.temp.lineJoin = "round";
    ctx.temp.lineCap = "round";

    ctx.temp.strokeStyle = brush_color;
    ctx.temp.lineWidth = brush_radius;
    if (!points || points.length < 2) return;
    let p1 = points[0];
    let p2 = points[1];
    ctx.temp.moveTo(p2.x, p2.y);
    ctx.temp.beginPath();
    for (var i = 1, len = points.length; i < len; i++) {
      var midPoint = mid_point(p1, p2);
      ctx.temp.quadraticCurveTo(p1.x, p1.y, midPoint.x, midPoint.y);
      p1 = points[i];
      p2 = points[i + 1];
    }

    ctx.temp.lineTo(p1.x, p1.y);
    ctx.temp.stroke();
  };

  let draw_fake_points = ({ points, brush_color, brush_radius }) => {
    if (!points || points.length < 2) return;

    ctx.temp_fake.lineJoin = "round";
    ctx.temp_fake.lineCap = "round";
    ctx.temp_fake.strokeStyle = "#fff";
    ctx.temp_fake.lineWidth = brush_radius;
    let p1 = points[0];
    let p2 = points[1];
    ctx.temp_fake.moveTo(p2.x, p2.y);
    ctx.temp_fake.beginPath();
    for (var i = 1, len = points.length; i < len; i++) {
      var midPoint = mid_point(p1, p2);
      ctx.temp_fake.quadraticCurveTo(p1.x, p1.y, midPoint.x, midPoint.y);
      p1 = points[i];
      p2 = points[i + 1];
    }

    ctx.temp_fake.lineTo(p1.x, p1.y);
    ctx.temp_fake.stroke();
  };

  // help funcs

  function getBrightness(hexColor) {
    const rgbColor = hexToRgb(hexColor);
    if (rgbColor) {
      const { r, g, b } = rgbColor;
      return (r * 299 + g * 587 + b * 114) / 1000;
    }
    return 0;
  }

  function getPixelColor(x, y) {
    const imageData = ctx.drawing.getImageData(x - 1, y, 1, 1);
    const [r, g, b, a] = imageData.data;
    return `rgb(${r}, ${g}, ${b})`;
  }

  function hexToRgb(hex) {
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result
      ? {
          r: parseInt(result[1], 16),
          g: parseInt(result[2], 16),
          b: parseInt(result[3], 16),
        }
      : null;
  }

  function rgbToHex(rgbString) {
    // Extracting r, g, b values from a string
    const [r, g, b] = rgbString
      .match(/\d+/g) // Find all the numbers in the string
      .map(Number); // Convert each group of digits to a numeric value

    // Function to convert a number to a two-digit hexadecimal value
    function toHex(num) {
      const hex = num.toString(16);
      return hex.length === 1 ? `0${hex}` : hex;
    }

    // Convert each r, g, b value to hexadecimal format and combine them
    return `#${toHex(r)}${toHex(g)}${toHex(b)}`;
  }

  // end

  let save_mask_line = () => {
    if (points.length < 1) return;
    points.length = 0;
    ctx.mask.drawImage(canvas.temp_fake, 0, 0, width, height);

    trigger_on_change();
  };

  let saveLine = () => {
    if (points.length < 1) return;

    lines.push({
      points: points.slice(),
      brush_color: brush_color,
      brush_radius,
    });

    if (mode !== "mask") {
      points.length = 0;
    }

    ctx.drawing.drawImage(canvas.temp, 0, 0, width, height);

    trigger_on_change();
  };

  let trigger_on_change = () => {
    const x = get_image_data();
    dispatch("change", x);
  };

  export function clear() {
    lines = [];
    clear_canvas();
    line_count = 0;

    return true;
  }

  function clear_canvas() {
    values_changed = true;
    ctx.temp.clearRect(0, 0, width, height);

    ctx.temp.fillStyle = mode === "mask" ? "transparent" : "#FFFFFF";
    ctx.temp.fillRect(0, 0, width, height);

    if (mode === "mask") {
      ctx.temp_fake.clearRect(0, 0, canvas.temp_fake.width, canvas.temp_fake.height);
      ctx.mask.clearRect(0, 0, width, height);
      ctx.mask.fillStyle = "#000";
      ctx.mask.fillRect(0, 0, width, height);
    }
  }

  function fill_canvas_color() {
    if (mode === "mask") {
      return;
    }

    ctx.temp.fillStyle = brush_color;
    ctx.temp.fillRect(0, 0, width, height);

    var startX = 0; // start point of the line (x-coordinate)
    var startY = height / 2; // start point of the line (y-coordinate)
    var endX = width; // end point of the line (x-coordinate)
    var endY = height / 2; // end point of the line (y-coordinate)

    ctx.drawing.beginPath();
    ctx.drawing.moveTo(startX, startY);
    ctx.drawing.lineTo(endX, endY);
    ctx.drawing.strokeStyle = brush_color;
    ctx.drawing.lineWidth = height; // make the line height of the canvas
    ctx.drawing.stroke();

    // Save line as two points in the lines array
    lines = [
      {
        points: [
          { x: startX, y: startY },
          { x: endX, y: endY },
        ],
        brush_color: brush_color,
        brush_radius: height,
      },
    ];
    line_count = 1;

    saveLine();
  }

  let loop = ({ once = false } = {}) => {
    if (mouse_has_moved || values_changed) {
      const pointer = lazy.getPointerCoordinates();
      const brush = lazy.getBrushCoordinates();
      draw_interface(ctx.interface, pointer, brush);
      mouse_has_moved = false;
      values_changed = false;
    }
    if (!once) {
      window.requestAnimationFrame(() => {
        loop();
      });
    }
  };

  $: brush_dot = brush_radius * 0.075;

  let draw_interface = (ctx, pointer, brush) => {
    ctx.clearRect(0, 0, width, height);

    let brushX = brush.x;
    let brushY = brush.y;

    if (posX && posY) {
      brushX = posX;
      brushY = posY;
    }
    // brush preview
    ctx.beginPath();
    ctx.fillStyle = brush_color;

    const brushOutlineEnabled = localStorage.getItem("brushOutline") === "true";
    if (brushOutlineEnabled) {
      const brightness = getBrightness(brush_color);
      ctx.strokeStyle = brightness > 128 ? "black" : "white"; // Change the strokeStyle based on brightness
      if (brush_radius > 180) {
        ctx.lineWidth = 4;
      } else if (brush_radius > 80) {
        ctx.lineWidth = 3; // Change the value to control the thickness of the border
      } else if (brush_radius > 20) {
        ctx.lineWidth = 2; // Change the value to control the thickness of the border
      } else {
        ctx.lineWidth = 1;
      }
    }

    ctx.arc(brushX, brushY, brush_radius / 2, 0, Math.PI * 2, true);
    ctx.fill(); // Filling the main point

    if (brushOutlineEnabled) {
      ctx.stroke(); // Main Point Boundary
    }

    // tiny brush point dot
    ctx.beginPath();
    ctx.fillStyle = catenary_color;
    ctx.arc(brushX, brushY, brush_dot, 0, Math.PI * 2, true);
    ctx.fill();
  };

  export function get_image_data() {
    return mode === "mask" ? canvas.mask.toDataURL("image/jpg") : canvas.drawing.toDataURL("image/jpg");
  }
</script>

<div class="wrap" bind:this={canvas_container} bind:offsetWidth={canvas_width} bind:offsetHeight={canvas_height}>
  {#if line_count === 0}
    <div transition:fade={{ duration: 50 }} class="start-prompt">Start drawing</div>
  {/if}
  {#each canvas_types as { name, zIndex }}
    <canvas
      key={name}
      style=" z-index:{zIndex};"
      class:lr={add_lr_border}
      class:tb={!add_lr_border}
      bind:this={canvas[name]}
      on:mousedown={name === "interface" ? handle_draw_start : undefined}
      on:mousemove={name === "interface" ? handle_draw_move : undefined}
      on:mouseup={name === "interface" ? handle_draw_end : undefined}
      on:mouseout={name === "interface" ? handle_draw_end : undefined}
      on:mouseout={name === "interface"
        ? () => {
            posX = posY = 0;
            localStorage.setItem("overCanvas", "false");
          }
        : undefined}
      on:blur={name === "interface" ? handle_draw_end : undefined}
      on:touchstart={name === "interface" ? handle_draw_start : undefined}
      on:touchmove={name === "interface" ? handle_draw_move : undefined}
      on:touchend={name === "interface" ? handle_draw_end : undefined}
      on:touchcancel={name === "interface" ? handle_draw_end : undefined}
      on:click|stopPropagation
    />
  {/each}
</div>

<style>
  canvas {
    display: block;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    margin: auto;
  }

  .lr {
    border-right: 1px solid var(--border-color-primary);
    border-left: 1px solid var(--border-color-primary);
  }

  .tb {
    border-top: 1px solid var(--border-color-primary);
    border-bottom: 1px solid var(--border-color-primary);
  }

  canvas:not(.color-picker-enabled):hover {
    cursor: none;
  }

  canvas.color-picker-enabled:hover {
    cursor: crosshair;
  }

  .wrap {
    position: relative;
    width: var(--size-full);
    height: var(--size-full);
    touch-action: none;
  }

  .start-prompt {
    display: flex;
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    justify-content: center;
    align-items: center;
    z-index: var(--layer-4);
    touch-action: none;
    pointer-events: none;
    color: var(--body-text-color-subdued);
  }
</style>
